import { useContext, useEffect, useState } from "react"
import { FileCtx } from "../ctx/file-ctx"
import { filterFile } from "../util/filterFile"
import { mostCommonTalkers } from "../util/mostCommon"
import { topTalkers } from "../util/topOnce"


export const useFilteredFiles=()=>{
    let {file,senderArray,setIsLoading,selectedSender}=useContext(FileCtx)
    const [sender,setSender]=useState<any>("504")
    const [port,setPort]=useState<any>("abcd")
    const [protocol,setProtocol]=useState<any>('504')
  
    const [newFile,setNewFile]=useState<any>(null)
    const [newBarFile,setNewBarFile]=useState<any>(null)
    const [appFile,setAppFile]=useState<any>(null)
    const [filteredFile,setFilteredFile]=useState<any>()
    const [filteredBarFile,setFilteredBarFile]=useState<any>(null)
    const [filteredAppFile,setFilteredAppFile]=useState<any>(null)

  
  useEffect(()=>{
    const init=async()=>{
      if(!file){
        setIsLoading(false)
        setNewFile(null)
        setAppFile(null)
        setFilteredAppFile(null)
        setNewBarFile(null)
        setFilteredBarFile(null)
        setFilteredFile(null)
        setPort('abcd')
        setProtocol('504')
        setSender('504')
      }
        if(file&&!newFile&&!newBarFile&& sender=== '504' ) {
          setIsLoading(true)
          await Promise.all([ 
            (async () => (setNewFile(await mostCommonTalkers(file,false))))(),
            (async () => (setAppFile(await mostCommonTalkers(file,true))))(),
            (async () => (setNewBarFile( await topTalkers(file,sender,protocol))))(), 
          ]).then(()=>{ 
            setFilteredFile(newFile)
            setFilteredBarFile(newBarFile)
            setFilteredAppFile(appFile)
            setIsLoading(false)
          }); 
        }
        if(newFile&&newBarFile&&appFile&&!filteredFile&&!filteredBarFile){
          if(senderArray.length===1){
            setFilteredFile(newFile)
          setFilteredBarFile(newBarFile)
          setFilteredAppFile(appFile)
          setIsLoading(false)
            setSender(senderArray[0])
          }else{
            setSender(selectedSender)
            Promise.all([
              (async () => (setFilteredFile(await filterFile(newFile,selectedSender,port,protocol))))(),
              (async () => (setFilteredAppFile(await filterFile(appFile,selectedSender,port,protocol))))(),
            (async () => (setFilteredBarFile( await topTalkers(file,selectedSender,protocol))))(), 
          ]).then(()=>{setIsLoading(false)});  

          }
        }
    }
     init()
  },[file,newFile,newBarFile,appFile,port,protocol,sender])
  
  
     //when file first loads create graph ready arrays of objects
    const onFilter=(senders:any,ports:any,protocols:any)=>{
      if(ports==='abcd'&&protocols==='504'&&senders==='504'){
        setFilteredFile(newFile)
        setFilteredBarFile(newBarFile)
        setFilteredAppFile(appFile)
        setIsLoading(false)
      }
  else if(ports!=='abcd'&&protocols==='504'&&senders==='504'){
    setIsLoading(true)
    Promise.all([
      (async () => (setFilteredFile(await filterFile(newFile,senders,ports,protocols))))(),
      (async () => (setFilteredAppFile(await filterFile(appFile,senders,ports,protocols))))(),
    (async () => (setFilteredBarFile(newBarFile)))(), 
  ]).then(()=>{setIsLoading(false)}); 
  }else if(ports!=='abcd'||protocols!=='504'||senders!=='504'){
    setIsLoading(true)
     Promise.all([
      (async () => (setFilteredFile(await filterFile(newFile,senders,ports,protocols))))(),
      (async () => (setFilteredAppFile(await filterFile(appFile,senders,ports,protocols))))(),
    (async () => (setFilteredBarFile( await topTalkers(file,senders,protocols))))(), 
  ]).then(()=>{setIsLoading(false)});   
  }
  
  setPort(ports)
  setProtocol(protocols)
  setSender(senders)
  setIsLoading(false)
    }


return {sender,port,protocol,filteredFile,filteredAppFile,filteredBarFile,onFilter}

}